@use 'components' as *;
@use 'constants' as *;

.calculator {
  .calcTitle{
    color: #dc1a55; 
  }

  font-family: raleway,sans-serif;
  input{
    font-family: raleway,sans-serif;
    font-size:13px;
    width:70px;
  }
  button{
    font-family: raleway,sans-serif;
    font-size:13px;
  }
  label{
    font-family: raleway,sans-serif;
    font-size:13px
  }
  table{
    font-family: raleway,sans-serif;
    font-size:13px;
    border: solid 1px #C9CDF2;
    border-radius: 2px;
    padding: 0.5rem 0rem 0.5rem 1rem;
    //margin:1em;
  }

  td, th, tr{
    font-family: raleway,sans-serif;
    font-size:13px;

  }
