.splash-screen {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.splash-screen .loading-dot {
  font-size: 100px;
  /* margin-top: -80px; */
  position: relative;
  -webkit-animation: ease-in-out infinite alternate;
          animation: ease-in-out infinite alternate;
  -webkit-animation-name: run;
          animation-name: run;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
}

@-webkit-keyframes run {
  0% {
    left: -90px;
    color: #eee;
  }
  50% {
    color: #666;
  }
  100% {
    left: 90px;
    color: #eee;
  }
}

@keyframes run {
  0% {
    left: -90px;
    color: #eee;
  }
  50% {
    color: #666;
  }
  100% {
    left: 90px;
    color: #eee;
  }
}

.tierGrid8{
  display: grid;
  grid-template-columns: repeat(7, minmax(200px, 1fr));
  grid-gap: 0px;
  gap: 0px;
  align-items: stretch;
  justify-content: center;
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: border-box;
}



/* 屏幕更窄时自动换行（你说用户缩窄才两行） */
@media (max-width: 1300px) {
  .tierGrid8{ grid-template-columns: repeat(4, minmax(0, 1fr)); }
}
@media (max-width: 900px) {
  .tierGrid8{ grid-template-columns: repeat(2, minmax(0, 1fr)); }
}
@media (max-width: 520px) {
  .tierGrid8{ grid-template-columns: 1fr; }
}

.tierCell{ min-width: 0; }

