@media print {

  @import url('path/to/print-styles.css');

  @page {
    size: Letter !important;
  }

  body {
    width: 100%;
    padding: 2cm;
    box-sizing: border-box;
  }

  input[type="checkbox"]:checked::before {
    content: "";
    display: inline-block;
    width: 10px;
    height: 10px;
    background-color: black;
  }


  legend span.color-icon {
    display: inline-block;
    width: 10px;
    height: 10px;
    background-color: red;

  }

}

